import React, { useContext } from "react";
import { Card, TextField, Button } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import { Formik } from "formik";
import * as Yup from "yup";

import { Authentication } from "../Authentication/Authentication";

const Login = () => {
  // All of the hooks needed to make the component work
  const authContext = useContext(Authentication);
  const history = useHistory();
  // This separates the sign in functions form the context.
  const { signInWithGoogle, signInWithEmailAndPassword } = authContext;

  // This hanldes the google login by wrapping it in an asynchronous function.
  const handleGoogleClick = async () => {
    try {
      await signInWithGoogle();
      history.push("/Search");
    } catch (e) {
      console.log(e);
    }
  };

  if (authContext.isAuthenticated) {
    history.push("/Search");
  }

  return (
    <Card id="login">
      <h4>Login</h4>
      {/* Using Formik, we set the expected values, then steup the logic and validation. After that its a pretty simple form */}
      <Formik
        initialValues={{
          Email: "",
          Password: "",
          Submit: null,
        }}
        validationSchema={Yup.object().shape({
          Email: Yup.string()
            .min(10, "Too short")
            .max(50, "Too long")
            .required("Must enter an email"),
          Password: Yup.string()
            .min(8, "Too short")
            .max(50, "Too long")
            .required("Must enter a password"),
          Favorite: Yup.string().min(4, "Too Short").max(100, "Too Long"),
        })}
        onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
          try {
            await signInWithEmailAndPassword(values.Email, values.Password);
            authContext.favoriteHanlder(values.Favorite);
            history.push("/Search");
            console.log(values.Email, values.Password);
          } catch (err) {
            console.error(err);
            setStatus({ success: false });
            setErrors({ submit: err.message });
            setSubmitting(false);
          }
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
        }) => (
          <Card className="Login Card">
            <form noValidate autoComplete="off" onSubmit={handleSubmit}>
              <TextField
                autoFocus
                id="outlined-basic"
                name="Email"
                className="textfield"
                label="email"
                variant="outlined"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.Email}
                required
                error={Boolean(touched.Email && errors.Email)}
                helpertext={touched.Email && errors.Email}
              />
              <TextField
                id="outlined-basic"
                name="Password"
                className="textfield"
                label="Password"
                variant="outlined"
                value={values.Password}
                onBlur={handleBlur}
                error={Boolean(touched.Password && errors.Password)}
                helpertext={touched.Password && errors.Password}
                onChange={handleChange}
                required
              />
              <Button
                className="button"
                variant="contained"
                disabled={errors.Username || errors.Password}
                type="Submit"
              >
                Login
              </Button>
            </form>
          </Card>
        )}
      </Formik>
      {/* This is the button that logs in with google. */}
      <Button
        fullWidth
        onClick={handleGoogleClick}
        size="large"
        variant="contained"
      >
        Sign in With Google
      </Button>
    </Card>
  );
};
export default Login;
